import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/dashboard',
    name: 'dashboard',
    component: () => import(/* webpackChunkName: "dashboard" */ '../views/Dashboard.vue'),
    beforeEnter: () => {
      if (sessionStorage.token==undefined || sessionStorage.token=='') return {name: 'home'};
    }
  },{
    path: '/vota/:id',
    name: 'vota',
    component: () => import(/* webpackChunkName: "dashboard" */ '../views/VotacionView.vue'),
    beforeEnter: () => {
      //if (sessionStorage.token==undefined || sessionStorage.token=='') return {name: 'home'};
    }
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
