export default function ({url, method='POST', body=null}){
    const options = {
        method: method,
        mode: 'cors',
        headers: {
            'Content-Type': 'application/json',
        }
    }
    if (sessionStorage.token!='' && sessionStorage.token!=undefined) options.headers.Authorization = 'Bearer '+sessionStorage.getItem('token');
    if (body) options.body = JSON.stringify(body);
    
    //const resp = fetch (`http://localhost:8000/${url}`, options)
    const resp = fetch (`https://esfm.d3cia.net/${url}`, options)
        .then(resp => {
            if (resp.status == 401){
                sessionStorage.removeItem('name');
                sessionStorage.removeItem('token');
                this.$router.push({name:'home'});
                return 0;
            }
            return resp.json();
        })
        .then(resp => {
            if (resp.state == 'Error') throw resp.message
            return resp;
        })
        .then(resp => {
            return resp;
        })
        .catch(error => {
            return {state:'Error', message:error, data:null};
        });
    return resp;
}
