<template>
  <div class="flex flex-col w-full justify-center home">
      <div class="flex justify-end w-full h-screen relative">
          <div class="absolute w-full h-full pt-5 z-10" style="background:linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,0.5) 0%, rgba(244,122,4,0.75) 100%);"></div>
          <div class="flex flex-1 justify-center items-center py-5 md:justify-end">
              <div class="flex flex-col text-right z-20">
                  <img src="../assets/img/load.gif" class="mb-8 mx-auto" style="height: 30vh;" draggable="false">
                  <p class="text-4xl font-bold"> Votaciones ESFM </p>
                  <div class="flex flex-col w-full">
                      <span class="text-xl"> Por y para nuestra comunidad </span>
                      <span class="text-sm" ref="blink" id="blink"> Votaciones para mejorar </span>
                  </div>
              </div>
              <img alt="ESFM-Anonymous" class="hidden w-1/2 md:block" src="../assets/img/cube.webp">
          </div>
      </div>
      <div class="flex flex-col justify-center items-center w-full min-h-[75vh] pt-14 bg-gray-300">
            <p class="text-2xl font-bold mb-5"> Registrate </p>
            <p class="text-base font-bold">
                Para generar tu registro deberás cumplir los siguientes pasos:
            </p>
               <div class="flex flex-col w-1/2 mx-auto py-10 md:flex-row md:w-5/6">
                    <div class="flex flex-col justify-center flex-1 bg-green-400 px-5 py-10">
                        <p class="font-bold"> Escanea el codigo QR en tu credencial </p>
                        <img src="../assets/img/qr.svg" class="w-1/2 mx-auto" draggable="false">
                    </div>
                    <div class="flex flex-col justify-center flex-1 bg-blue-400 px-5 py-10">
                        <p class="font-bold"> Copia la URL que obtuviste e ingresala </p>
                        <img src="../assets/img/url.svg" class="mx-auto mt-3" draggable="false">
                    </div>
                    <div class="flex flex-col justify-center flex-1 bg-red-400 px-5 py-10">
                        <p class="font-bold"> Tendrás accesso inmediato a las votaciones </p>
                        <img src="../assets/img/acceso.svg" class="w-1/2 mx-auto" draggable="false">
                    </div>
                </div>
      </div>
      <div class="flex flex-col items-center justify-center w-full h-[75vh] bg text-white p-10">
          <p class="text-2xl font-bold mb-5"> Únete a las votaciones </p>
          <p class="text-base font-bold">
              Solo los estudiantes verificados puede acceder a las encuestas para emitir su voto.
          </p>
          <div class="flex w-full mt-10 my-5 md:w-2/3">
              <div class="input">
                  <input type="text" placeholder="URL o sello único" ref="boleta" id="boleta" @blur="filled" @keyup.enter="login" v-model="sello" autocomplete="off">
                  <label class="label" for="boleta"> URL o sello único </label>
              </div>
          </div>
          <button class="font-bold text-xl bg-green-600 rounded-lg p-2 w-1/2 mx-auto md:w-1/3" @click="login" ref="btn_login"> Ingresar </button>
      </div>
      <div class="w-full h-5 bg p-5">

      </div>
  </div>
</template>

<script>
import query from '@/mixins/query'

export default {
  name: 'HomeView',
  data (){
      return {
          blink: [
              'El anonimato es fundamental',
              'Voto libre y secreto',
              'Expresión libre y sin miedo',
              'Somos una misma comunidad',
              0
          ],
          sello: '',
      }
  },
  methods: {
      filled (e){
          if (e.target.value != '') e.target.nextElementSibling.classList.add('filled');
          else e.target.nextElementSibling.classList.remove('filled');
      },
      async login (){
          this.$refs.btn_login.disabled = true;
          this.$refs.btn_login.textContent = "Verificando identidad";

          this.sello = (this.sello.indexOf('h=')==-1)? this.sello : this.sello.split('h=')[1];
          const {state, data} = await query({url:'login', method:'POST', body:{sello:this.sello}});
          if (state=='Success'){
              sessionStorage.setItem('token', data.token);
              sessionStorage.setItem('name', data.name);
              this.$router.push({name: 'dashboard'});
          }
          else alert('Datos incorrectos');
          this.$refs.btn_login.textContent = "Iniciar";
          this.$refs.btn_login.disabled = false;
      }
  },
  mounted (){
      setInterval(() => {
          this.blink[4] = (this.blink[4]+1)%4;
          this.$refs.blink.innerHTML = this.blink[this.blink[4]];
      }, 2500);
  },
  components: {
  }
}
</script>

