    .input .label {
        @apply absolute left-2 top-4;
        transition: all 0.2s linear;
        padding: 0 2px;
        z-index: 1;
        background: none;
        color: #111;
    }

    .input input {
        @apply w-full h-full text-gray-600;
        padding: 0.8rem;
        border: 2px solid #2f2c45;
        border-radius: 5px;
        font-size: 18px;
        outline: none;
        transition: all 0.3s linear;
    }
        .input input:focus {
            border: 2px solid #7e4ccb;
        }
        .input input:focus + .label, .filled {
            top: -10px !important;
            color: #7e4ccb;
            font-size: 14px;
            font-weight: 700;
        }
        .input input::placeholder {
            font-size: 16px;
            opacity: 0;
            transition: all 0.3s linear;
